body {
  margin: 0;
  padding: 0;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  text-shadow: 1px 1px black;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(black), to(purple));
  background: -webkit-linear-gradient(black, purple);
  background: -o-linear-gradient(black, purple);
  background: linear-gradient(black, purple);
  height: 100%;
  text-align: center;
  background-size: 100%;
  background-attachment: fixed;
  background-position: top;
  color: white;
}

html {
  height: 100%;
}



